*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  line-height: 1.2; }

body {
  font-size: 1em;
  color: #212121; }

.clearfix::before, .clearfix::after {
  content: "";
  display: table; }

.clearfix::after {
  clear: both; }

.no-padding {
