.no-margin {
  margin: 0; }

.main-header__title {
  padding: 25px 0 18px; }

.main-header__brand-logo {
  max-width: 100px;
  display: block;
  margin: 0 auto; }

.authorized .main-header {
  height: 130px;
  background: #fff;
  border-bottom: 1px solid #ffc107; }
