[ui-view="main"],
[ui-view="services"] {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden; }
  [ui-view="main"].ng-enter, [ui-view="main"].ng-leave,
  [ui-view="services"].ng-enter,
  [ui-view="services"].ng-leave {
    transition: 0.2s opacity linear, 0.2s transform linear; }
  [ui-view="main"].ng-enter,
  [ui-view="services"].ng-enter {
    opacity: 0;
    transform: scale3d(0.98, 0.98, 0.98); }
  [ui-view="main"].ng-enter-active,
  [ui-view="services"].ng-enter-active {
    opacity: 1;
    transform: scale3d(1, 1, 1); }
  [ui-view="main"].ng-leave,
  [ui-view="services"].ng-leave {
    opacity: 0;
    transform: scale3d(0, 0, 0); }
